<script>
  import _ from 'lodash';
	import cities from '../assets/js/cities';
	import Events from '../components/Events.svelte';
	import CalLocationSelect from '../components/CalLocationSelect.svelte';
	import CalMonth from '../components/CalMonth.svelte';

	let calendar = {
		weeks: [],
		today: {},
		selectedDate: {},
		selectedLocation: { name: 'Select Location...', code: -1 },
		calDates: {},
		isDateSelected: true,
    upcoming: [],
		months: [{ name: 'January', days: 31 }, { name: 'February', days: 28 }, { name: 'March', days: 31 }, { name: 'April', days: 30 }, { name: 'May', days: 31 }, { name: 'June', days: 30 }, { name: 'July', days: 31 }, { name: 'August', days: 31 }, { name: 'September', days: 30 }, { name: 'October', days: 31 }, { name: 'November', days: 30 }, { name: 'December', days: 31 }],
		days: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday']
	};

	const actualise = () => {
		const today = new Date(),
			location = localStorage.location;

		let todayObj = {
			date: today.getDate(),
			day: today.getDay(),
			month: today.getMonth(),
			year: today.getFullYear() 
		};

		parseDate(todayObj);

		calendar.today = todayObj;

		if (location) {
			calendar.selectedLocation = _.find(cities, city => city.code === parseInt(location));
		}
	}

	const selectLocation = () => {
		localStorage.location = calendar.selectedLocation.code;
		calendar.month = -1;

		return new Promise(resolve => {
			if (!calendar.calDates[calendar.selectedLocation.code]) {
				import('../assets/cal/location-' + calendar.selectedLocation.code + '.js')
					.then(locationImport => {
						calendar.calDates[calendar.selectedLocation.code] = locationImport.default;
						resolve();
					});
			} else {
				resolve();
			} 
		});
	}

	const loadEvents = () => {
		_.each(calendar.calDates[calendar.selectedLocation.code], (date, dateText) => {
			let dateTextSplit = dateText.split('-'), imgEv;

			date.dateText = dateText;
			date.year = parseInt(dateTextSplit[0]);
			date.month = parseInt(dateTextSplit[1]) - 1;
			date.date = parseInt(dateTextSplit[2]);

			imgEv = _.find(date.events, ev => ev.hasOwnProperty('url'));

			if (imgEv) date.url = imgEv.url;
		});

		if (calendar.selectedDate && calendar.selectedDate.date) {
			calendar.dateToSelect = calendar.selectedDate;
		} else {
			calendar.dateToSelect = calendar.today;
		}
	}

	const setMonth = ({ date, dayIndex, month, year }) => {
		const fillMonth = (weekNo, date) => {
			let params = { calDates: calendar.calDates[calendar.selectedLocation.code] },
				itrDate;

			calendar.weeks.push([]);

			for (let i = 0; i < 7; i++) {
				itrDate = { date, month: calendar.month, year: calendar.year, dayIndex: i };

				if ((weekNo == 0 && i < calendar.day1Index) || calendar.loaded) {
					calendar.weeks[weekNo].push('');
				} else {
					parseDate(itrDate, params);
					calendar.weeks[weekNo].push(itrDate);
				
					if (date === calendar.months[calendar.month].days) {
						calendar.loaded = true;
						calendar.dayLastIndex = i;
					}
				
					date++;
				}
			}

			if (!calendar.loaded) {
				fillMonth(weekNo + 1, date);
			}
		}

		const checkFeb = () => {
			if ((calendar.year % 4) === 0) {
				calendar.months[1].days = 29;
			} else {
				calendar.months[1].days = 28;
			}
		}

		if (calendar.year !== year) {
			checkFeb();
		}

		calendar.year = year;
		calendar.month = month;
		calendar.weeks = [];
		calendar.loaded = false;
		calendar.day1Index = date === 1 ? dayIndex : ((dayIndex - (date % 7)) + 1 + 7) % 7;

		fillMonth(0, 1);
	}

	const getPrev = () => {
		let year = calendar.month !== 0 ? calendar.year : calendar.year - 1,
			month = (calendar.month + 11) % 12
	
		setMonth({
			date: calendar.months[month].days,
			dayIndex: (calendar.day1Index + 6) % 7,
			month,
			year
		});
	}

	const getNext = () => {
		let year = calendar.month !== 11 ? calendar.year : calendar.year + 1;

		setMonth({
			date: 1,
			dayIndex: (calendar.dayLastIndex + 1) % 7,
			month: (calendar.month + 1) % 12,
			year
		});
	}

	const selectDate = (date) => {
		const setSelected = (date) => {
			date.selected = date.selected ? false : true;
			setColor(date);
		}

		let prevDate = calendar.selectedDate;

		if (date.dateText && !date.dayIndex) date.dayIndex = new Date(date.dateText).getDay();

		if (prevDate.dateText) {
			setSelected(prevDate);
		}
		
		setSelected(date);
		calendar.selectedDate = date;
		calendar.isDateSelected = true;

		if (date.month != calendar.month || date.year != calendar.year || calendar.weeks.length < 1) {
			setMonth(date);
		}
	}

	const setColor = (date) => {
		if (date.today) {
			date.bg = 'rgba(255, 165, 0, 0.7)';
		} else if (date.selected) {
			date.bg = 'rgba(255, 165, 0, 0.4)';
		} else {
			date.bg = '';
		}

		if (date.dayIndex === 0) {
			date.color = 'rgb(255, 0, 0)';
		}
	}

	const parseDate = (date, params) => {
		date.dateText = date.year + '-' + ('0' + (date.month + 1)).slice(-2) + '-' + ('0' + date.date).slice(-2);

		if (params) {
			if (params.calDates && params.calDates[date.dateText]) {
				_.merge(date, params.calDates[date.dateText]);
			}
			
			if (calendar.today.dateText === date.dateText) {
				date.today = true;
				calendar.today = date;
			}
			
			if (calendar.selectedDate.dateText === date.dateText) {
				date.selected = true;
				calendar.selectedDate = date;
			}
		}

		setColor(date);
	}

	const loadCalendar = () => {
		selectLocation().then(() => {
			loadEvents();
			selectDate(calendar.dateToSelect);
		});
	}

  actualise();
	if (calendar.selectedLocation && calendar.selectedLocation.code !== -1) {
		loadCalendar();
	}
</script>
<div class="main">
  <div class="header">
		{#if calendar.loaded}
			<div class="monthSelect row flex-md flex-lg shadow-10">
				<div class="row main-center cross-center clickable flex-md flex-lg" on:click={ getPrev }>&#9664;</div>
				<div class="row main-center cross-center clickable flex">{ calendar.months[calendar.month].name } { calendar.year }</div>
				<div class="row main-center cross-center clickable flex-md flex-lg" on:click={ getNext }>&#9654;</div>
			</div>
		{/if}
    <CalLocationSelect calendar={ calendar } loadCalendar={ loadCalendar } />
  </div>
	{#if calendar.loaded}
		<div class="container">
			<CalMonth calendar={ calendar } selectDate={ selectDate } />
			{#if calendar.selectedDate.dateText}
				<Events calendar={ calendar } selectDate={ selectDate } />
			{/if}
		</div>
	{/if}
</div>
<style lang="scss">
  @import '../assets/css/app.scss';

	.main {
		width: 100%;
		padding: 10px;
	}

	.header {
		display: flex;

		@include md-lg {
			flex-direction: row;
		}

		@include sm {
			flex-direction: column-reverse;
		}
	}

	.monthSelect {
		border-radius: 3px;
		margin-bottom: 20px;
		height: 50px;

		& > div:nth-child(odd) {
			@include sm {
				width: 20%;
				flex: none;
			}
		}
	}

	.container {
		@include md-lg {
			display: flex;
			flex-direction: row;
			min-height: calc(100vh - 166px);
			max-height: calc(100vh - 166px);
		}

		@include sm {
			display: flex;
			flex-direction: column;
		}
	}
</style>