<script>
  import _ from 'lodash';
  import gurus from '../assets/js/parampara';

  export let params;

  let bookName = '';

  _.each(gurus, guru => {
    if (guru.books && guru.books.length) {
      _.each(guru.books, book => {
        if (book.code === params.code) {
          bookName = book.name;
        }
      });
    }
  });
</script>
<div class="container column h-100">
  <embed src="../assets/pdf/{ params.code }.pdf" type="application/pdf" width="100%" height="99.4%" />
</div>
