<script>
  export let calendar;
  export let selectDate;
</script>
<div class="col month">
  <div class="row">
    {#each calendar.days as day, dayIndex}
      <div class="row flex main-center day {dayIndex === 0 ? 'fc-dark-red' : ''}">{ day.substring(0, 3)} </div>
    {/each}
  </div>
  {#each calendar.weeks as week}
    <div class="row flex">
      {#each week as date}
        <div class="row flex">
          {#if date}
            <div
              class="flex row main-center cross-center date clickable"
              style="background-color: {date.bg}; color: {date.color}; --eventBgImg: {date.url}"
              on:click={() => selectDate(date)} >
              <!-- Actual date -->
              {date.date}
              <!-- Dot for Ekadashi -->
              {#if date.ekadashi}
                <div class="dot red row main-center cross-center"></div>
              {/if}
              <!-- Dot for other events -->
              {#if date.events && ((date.events.length > 0) || (date.special))}
                <div class="dot blue row main-center cross-center">
                  {date.special ? date.events.length + 1 : date.events.length}
                </div>
              {/if}
          </div>
          {/if}
        </div>
      {/each}
    </div>
  {/each}
</div>
